import * as React from "react"
// import { Link } from "gatsby"
// import Container from "../../components/Container"
import Layout from "../../components/Layout"
import WaveHeader from "../../components/helpers/WaveHeader"
import GoogleForm from "../../components/helpers/Form" 
import { useStaticQuery, graphql } from "gatsby" 


const Html = () => {
  const data = useStaticQuery(graphql`
  query clientForm{ 
      site {  
        siteMetadata{ 
            forms {  
              clienteNuevo
              cliente
              registro
              contacto
              sugerencias
            }
          } 
      }
    }
  `) 
   console.log(data);
  return (
    <Layout> 
     <WaveHeader fill="#00000"/>
     saddasd
     <GoogleForm src={data.site.siteMetadata.forms.cliente}/>
    </Layout>
  )
}

export default Html
 
